export function getQuestion(callback, id) {
    if (id === undefined) { 
        setTimeout(() => {
            callback(questions);
        }, lag)
        return;
    }
    setTimeout(() => {
        callback(questions[id]);
    }, lag)
}

export function postQuestion(question, callback) { 
    question._id = questions.length
    setTimeout(() => {
        callback(questions);
    }, lag)
}


export function getQuestionAllInfo(id, callback) { 
    setTimeout(() => {
        console.log(id)
        const data = { userId: 123, question: questions[id], testCases: testCases.filter(tc => tc.owner_id == id)}
        callback(data);
    }, lag)
}
