export function addTestCase(testCase, cb) { 
    testCase._id = testCases.length;
        setTimeout(() => {
            cb(testCases.filter(tc => tc.owner_id === testCase.owenr_id));
        }, lag)
}

export function getTestCases(question_id, cb) {
    setTimeout(() => {      
            cb(testCases.filter(tc => tc.owner_id === question_id));
        }, lag)

}

